import {ChangeDetectionStrategy, Component} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BaseConfigComponent} from '../../../../../helpers/base-config.component';
import {FORM} from '@app/modules/clubmodel/club-model.form';
import {ClubModel} from '@myclapp/myclapp-server';
import {BehaviorSubject} from 'rxjs';

import {ClubModelService, UserService} from '../../../../../services';

@Component({
    selector: 'sb-clubdata',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './club-model.component.html',
    styleUrls: ['./club-model.component.scss'],
})
export class ClubModelComponent extends BaseConfigComponent<ClubModel>{
    _form: FormGroup;
    formLayout: any[] = FORM;
    _mappedModel$: BehaviorSubject<ClubModel> = new BehaviorSubject<ClubModel>(
        {
            secondary_color_hex: '',
            city: '',
            street: '',
            primary_color_hex: '',
            name: '',
            postcode: '',
        }
    );


    constructor(public fb: FormBuilder, public clubModelService: ClubModelService, public userService: UserService) {
        super(ClubModelComponent.name)
        this._logger.debug('Constructing component')
        this._form = this.fb.group({
            name: [''],
            street: [''],
            postcode: ['', Validators.required],
            city: ['', Validators.required],
            primary_color_hex: [''],
            secondary_color_hex: ['']
        });
        this._initialStatus$.next({status: 'pending'})

        this.clubModelService.get().subscribe( data => {
            this._initialStatus$.next({status: 'success'})
            this._mappedModel$.next(data)
            setTimeout(() => {
                this._initialStatus$.next({status: 'idle'})
            },300);
        });


        this._mappedModel$.subscribe(data => {
            this._logger.debug('_mappedModel updated ',data)
            try {
                for (const [key, value] of Object.entries(data)) {
                    this._form.controls[key]?.setValue(value);
                }
            } catch (e) {
                this._logger.error('Failed initializing of component data', e)
                this._initialStatus$.next({status: 'error'})
                throw e
            }
        });

        this._form.controls['primary_color_hex'].valueChanges.subscribe(value => {
            let inputField = document.getElementById('inputPrimaryColor');
            let colorTrans = ClubModelComponent._hexToRGB(value, 0)
            let colorFull = ClubModelComponent._hexToRGB(value, 1)
            if (inputField != null && colorFull != '' && colorTrans != '') {
                this._logger.info('entered if', [colorFull, colorTrans])
                inputField.style.background = 'linear-gradient(to right, ' + colorTrans + ', ' + colorFull + ')'
            }
        });
        this._form.controls['secondary_color_hex'].valueChanges.subscribe(value => {
            let inputField = document.getElementById('inputSecondaryColor')
            let colorTrans = ClubModelComponent._hexToRGB(value, 0)
            let colorFull = ClubModelComponent._hexToRGB(value, 1)
            if (inputField != null && colorFull != '' && colorTrans != '') {
                inputField.style.background = 'linear-gradient(to right, ' + colorTrans + ', ' + colorFull + ')'
            }
        });
    }

    protected _submitData() {
        const data: ClubModel = {
            ...this._form.value,
        };
        this.clubModelService.submit(data).subscribe(res => {
            this._logger.debug('Received submit result', res)
            this._submitStatus$.next({status: 'success'})
            setTimeout(() => {
                this._submitStatus$.next({status: 'idle'})
            }, 300)
            this._mappedModel$.next(res)
        })
    }

    private static _hexToRGB(hex: string, alpha: number): string {
        if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
            const r = parseInt(hex.slice(1, 3), 16),
                g = parseInt(hex.slice(3, 5), 16),
                b = parseInt(hex.slice(5, 7), 16);

            if (alpha) {
                return 'rgba(' + r + ', ' + g + ', ' + b + ', ' + alpha + ')';
            } else {
                return 'rgba(' + r + ', ' + g + ', ' + b + ',0)';
            }
        } else return ''


    }


}
